@import './styles/variables';

.App {
  margin: 0 auto;
  text-align: center;
  background-color: whitesmoke;
  max-width: 375px;
  width: 100%;
  font-family: 'Rubik', sans-serif;
  line-height: 24px;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  padding: 0 30px;

  h1 {
    font-weight: 500;
    color: black;
    font-size: 40px;
    line-height: normal;
  }

  h2 {
    font-weight: 500;
    font-size: 32px;
    line-height: normal;
  }

  h3 {
    font-size: 20px;
    color: hsl(229, 8%, 60%);
    line-height: normal;
    padding: 20px;
    border-bottom: 1px solid;
    position: relative;
    cursor: pointer;

    &:first-of-type {
      border-top: 1px solid;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: -1px; // Położenie tuż pod tekstem
      left: 0;
      right: 0;
      height: 3px; // Grubość kładki
      background-color: $lightRed; // Kolor kładki
      transform: scaleX(0); // Zaczyna niewidoczne
      transition: transform 0.3s ease-in-out; // Animacja rozciągania
    }

    &:hover::after,
    &.active::after {
      transform: scaleX(0.5);
    }
  }

  p {
    color: $grayish;
    margin: 20px 0 30px;
    line-height: 1.5;
  }
  
}

.logo {
  height: 50px;
}

.button {
    background-color: $lightBlue;
    outline: transparent;
    padding: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
    border: none;
    transition: background-color 0.3s ease;
    color: white;
  }

